import React from 'react';

import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import NavIzquierdo from './NavIzquierdo';
import AudioPlayer from './AudioPlayer';
import Artista from './Artista';
import Home from './Home';
import NotFound from './NotFound';

class App extends React.Component {
  state = {
	  artistas: [
		  {nombre:"the weeknd",id:"1"},
		  {nombre:"the weeknd2",id:"2"},
		  {nombre:"the weeknd3",id:"3"},
		]
  };

  

  render() {

    return (
		<Router>
			<div className="text-white">
				<div className="d-flex justify-content-between">
					<NavIzquierdo></NavIzquierdo>
					<div id="navSpace" className="col-sm-1"></div>
					<Switch>
						<Route exact path="/">
							<Home artistas={this.state.artistas}></Home>
						</Route>
						<Route exact path="/artista">
							<Artista></Artista>
						</Route>
						<Route path="*">
							<NotFound/>
						</Route>
					</Switch>
				</div>
				<div className="d-flex fixed-bottom">
					<AudioPlayer></AudioPlayer>
				</div>
			</div>
		</Router>
    );
  }
}

export default App;
